
html,body{height:100%
}
body{background:#fff3c4;color:#666
}
html,body,ul,li,h1,h2,h3,h4,h5,h6{margin:0;padding:0
}
li{list-style:none
}
a{text-decoration:none;color:#b8944d;-webkit-transition:.5s;transition:.5s
}
a:hover{color:#e3a936
}
button{outline:none
}
.box{display:-webkit-box;display:-ms-flexbox;display:flex
}
.box .items{-webkit-box-sizing:border-box;box-sizing:border-box;padding:50px;-webkit-box-flex:100%;-ms-flex:100%;flex:100%;-ms-flex-negative:0;flex-shrink:0;font-size:18px
}
.box .items .title{font-size:26px
}
.box .items img{width:100%
}
.box.items_2 .items{-webkit-box-flex:50%;-ms-flex:50%;flex:50%
}
.gbtn{display:inline-block;-webkit-box-sizing:border-box;box-sizing:border-box;padding:0 20px;height:50px;line-height:50px;text-align:center;background:#b8944d;color:white;border:1px solid #b8944d
}
.gbtn:hover{color:#b8944d;background:white
}
.gbtn.delete{border:1px solid #b8564d;background:#b8564d
}
.gbtn.delete:hover{color:#b8564d;background:white
}
.btn_group{display:-webkit-box;display:-ms-flexbox;display:flex
}
.btn_group .gbtn+.gbtn{margin-left:10px
}
.btn_group.right{-webkit-box-pack:end;-ms-flex-pack:end;justify-content:flex-end
}
.btn_group.left{-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start
}
table{width:100%;table-layout:fixed
}
table+.gbtn{margin-top:20px;float:right
}
table tr th{padding:20px 15px;text-align:left;font-weight:500;font-size:16px;color:#ffffff;text-transform:uppercase
}
table tr td{padding:0 15px;text-align:left;vertical-align:middle;font-weight:300;line-height:50px;font-size:16px;color:#666;border-bottom:1px solid #b8944d
}
table tr td input{border:1px solid #b8944d;height:30px
}
.tbl_header{margin-top:50px;background-color:#b8944d
}
.tbl_content{margin-top:0
}
.community{width:950px;margin:0 auto
}
.community th.title,.community th.delete,.community td.title,.community td.delete{width:120px;text-align:center
}
.community th.no,.community td.no{width:50px;text-align:center
}
.community input{width:100%
}
.community .btn_group{margin-top:30px
}
.community .gbtn.delete{height:30px;line-height:30px
}
#wrap{width:1320px;margin:0 auto
}
#wrap #header{position:relative;height:60px
}
#wrap #header #top_logo{float:left
}
#wrap #header #top_logo a{height:60px;line-height:60px
}
#wrap #header #top_nav{position:relative;height:60px;float:right
}
#wrap #header #top_nav ul{float:left
}
#wrap #header #top_nav ul li{position:relative;float:left;margin-left:30px
}
#wrap #header #top_nav ul li:first-child{margin-left:0
}
#wrap #header #top_nav ul li a{display:inline-block;width:100%;height:100%;line-height:60px;height:60px
}
#wrap #header #top_nav ul li:hover .sub_menu{visibility:visible;top:100%;opacity:1
}
#wrap #header #top_nav ul li .sub_menu{position:absolute;border:1px solid #b8944d;visibility:hidden;top:calc( 100% - 3px);opacity:0;-webkit-transition:.5s;transition:.5s;z-index:500
}
#wrap #header #top_nav ul li .sub_menu li{width:100%;margin:0;background:white;-webkit-box-sizing:border-box;box-sizing:border-box;padding:0 10px;width:100%;min-width:150px;-webkit-transition:.5s;transition:.5s
}
#wrap #header #top_nav ul li .sub_menu li a{line-height:40px;height:40px
}
#wrap #header #top_nav ul li .sub_menu li:hover{background:#b8944d
}
#wrap #header #top_nav ul li .sub_menu li:hover a{color:white
}
#wrap #footer{color:#b8944d;font-size:14px
}
#wrap #footer .footer_top{padding:60px 0 30px
}
#wrap #footer .container{margin:0 auto;width:100%
}
#wrap #footer .container .row{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-ms-flex-wrap:wrap;flex-wrap:wrap
}
#wrap #footer .container .row .column{-webkit-box-flex:25%;-ms-flex:25%;flex:25%;-webkit-box-sizing:border-box;box-sizing:border-box;padding:0 30px;margin-bottom:30px
}
#wrap #footer .container .row .column.footer_logo{-webkit-box-flex:100px;-ms-flex:100px;flex:100px
}
#wrap #footer .container .row .column.footer_logo h3{font-size:34px;margin:0 0 20px 0;padding:2px 0 2px 0px;line-height:1;font-weight:700
}
#wrap #footer .container .row .column.news input[type=search]{border:0;padding:6px 8px;width:65%
}
#wrap #footer .container .row .column.news input[type=submit]{background:#b8944d;border:0;width:35%;padding:6px 0;text-align:center;color:#ffffff;cursor:pointer
}
#wrap #footer .container .row .column h4{position:relative;font-size:14px;font-weight:700;color:#b8944d;text-transform:uppercase;padding-bottom:12px;margin-bottom:20px
}
#wrap #footer .container .row .column h4:before,#wrap #footer .container .row .column h4:after{content:'';position:absolute;left:0;bottom:0;height:3px
}
#wrap #footer .container .row .column h4:before{right:0;background:#997939
}
#wrap #footer .container .row .column h4:after{width:90px;background:#b8944d
}
#wrap #footer .container .row .column ul li{border-bottom:1px solid #b8944d;padding:10px 0
}
#wrap #footer .container .row .column ul li:nth-child(1){padding-top:0
}
#wrap #footer .container .row .column p{line-height:26px
}
#wrap #footer>.container{padding:30px 0;text-align:center
}
.mainbanner{height:550px;background:url(/static/img/main_banner.f4a3fbb.jpg) no-repeat;background-size:cover
}
.inner:nth-child(1){margin-top:50px
}
body.subpage main .container{display:-webkit-box;display:-ms-flexbox;display:flex
}
body.subpage main .container #lnb{-webkit-box-flex:230px;-ms-flex:230px;flex:230px;-ms-flex-negative:0;flex-shrink:0;-webkit-box-flex:0;-ms-flex-positive:0;flex-grow:0;-webkit-box-sizing:border-box;box-sizing:border-box;padding-right:50px
}
body.subpage main .container #lnb ul li{border-bottom:1px solid #b8944d
}
body.subpage main .container #lnb ul li a{display:inline-block;width:100%;height:100%;line-height:50px;font-size:18px;-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-transition:0.5s;transition:0.5s
}
body.subpage main .container #lnb ul li a:hover{padding-left:20px
}
body.subpage main .container #lnb ul li a.on{color:white;background:#b8944d;padding-left:20px
}
body.subpage main .container .sub_title span{position:relative;display:inline-block;padding-bottom:8px;line-height:43px
}
body.subpage main .container .sub_title span:after{content:'';position:absolute;height:3px;background:#b8944d;bottom:0;left:0;right:0
}
body.subpage main .container .inner+.inner{margin-top:50px;padding-top:50px;border-top:1px solid #b8944d
}
body.subpage main .container .inner .box{-ms-flex-wrap:wrap;flex-wrap:wrap
}
body.subpage main .container .inner .box .items{padding:50px 0
}
#app{font-family:'Avenir', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;color:#2c3e50;margin-top:60px
}

h1,h2{font-weight:normal
}
ul{list-style-type:none;padding:0
}
li{display:inline-block;margin:0 10px
}
a{color:#42b983
}
